<template>
  <div>
    <el-container>
      <el-header style="text-align: left;padding-left: 0px;margin-top: 10px">
        <el-tag size="medium">请选择部门:</el-tag>
        <el-select size="mini" v-model="depId" placeholder="请选择" @change="getDepartmentId">
          <el-option
            v-for="item in deps"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
        <el-table 
          ref="multipleTable"
          :data="employees"
          height="400"
          border
          stripe
          @selection-change=""
          style="width:100%;margin-top:20px;">
          <el-table-column 

            type="index"
           >
            
          </el-table-column>
          <el-table-column 
            prop="name"
            label="姓名"
            width="100"
            >
          </el-table-column>
          <el-table-column 
            prop="gender"
            label="性别"
            width="50"
            >
            
          </el-table-column>
          <el-table-column 
            prop="workID"
            label="工号"
            >
            
          </el-table-column>
          <el-table-column 
            prop="department.name"
            label="部门"
            >
            
          </el-table-column>
          <el-table-column 
            prop="basicSalary"
            label="基本工资"
            >
            
          </el-table-column>
          <el-table-column 
            prop="tc"
            label="提成"
            >
            
          </el-table-column>
          <el-table-column 
            prop="jbf"
            label="加班费"
            >
            
          </el-table-column>
          <el-table-column 
            prop="other"
            label="其他"
            >
            
          </el-table-column>
          <el-table-column 
            prop="salary.basicSalary"
            label="工资总额"
            >
            
          </el-table-column>
          <el-table-column 
            prop="phone"
            label="联系电话"
            width="120"
            >
            
          </el-table-column>
          <el-table-column 
            prop="isOpen"
            label="是否已发放"
            width="120"
            >
            
          </el-table-column>
          <el-table-column 
            label="操作"
            >
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="setSalaryTable(scope.row)">编辑</el-button>
              <el-dialog title="工资表管理" :visible.sync="salaryTableVisible">
                <div slot="footer" class="dialog-footer">
                  <el-form ref="form" :model="emp" label-width="80px" label-position="right" size="mini">
                  <el-row>
                    <el-col :span="10">
                      <el-form-item label="姓名">
                        <el-input v-model="emp.name"></el-input>
                      </el-form-item>
                    </el-col>
                    <el-col :span="10">
                      <el-form-item label="部门">
                        <el-input v-model="emp.department.name"></el-input>
                      </el-form-item>
                    </el-col>
                  </el-row>
                  <el-row>
                    <el-col :span="10">
                      <el-form-item label="电话">
                        <el-input v-model="emp.phone"></el-input>
                      </el-form-item>
                    </el-col>
                    <el-col :span="10">
                      <el-form-item label="工号">
                        <el-input v-model="emp.workID"></el-input>
                      </el-form-item>
                    </el-col>
                  </el-row>
                  <el-row>
                    <el-col :span="10">
                      <el-form-item label="基本工资">
                        <el-input v-model="emp.salary.basicSalary=null ? '':emp.salary.basicSalary"></el-input>
                      </el-form-item>
                    </el-col>
                    <el-col :span="10">
                      <el-form-item label="提成">
                        <el-input v-model="emp.salary.bonus"></el-input>
                      </el-form-item>
                    </el-col>
                  </el-row>
                  <el-row>
                    <el-col :span="10">
                      <el-form-item label="加班费">
                        <el-input v-model="emp.salary.lunchSalary"></el-input>
                      </el-form-item>
                    </el-col>
                    <el-col :span="10">
                      <el-form-item label="其他">
                        <el-input v-model="emp.salary.trafficSalary"></el-input>
                      </el-form-item>
                    </el-col>
                  </el-row>
                  <el-row>
                    <el-col :span="10">
                      <el-form-item label="工资总额">
                        <el-input v-model="emp.salary.allSalary"></el-input>
                      </el-form-item>
                    </el-col>
                    <el-col :span="10">
                      <el-form-item label="备注">
                        <el-input v-model="emp.workID"></el-input>
                      </el-form-item>
                    </el-col>
                  </el-row>
                  </el-form>
                  <el-button @click="salaryTableVisible = false">取 消</el-button>
                  <el-button type="primary" @click="saveSalaryTableInfo">确 定</el-button>
                </div>
              </el-dialog>
            </template>
          </el-table-column>
        </el-table>
        <pagination style="text-align: right;" :total="total" @pageSize="setPageSize" @currentPage="setCurrentPage"></pagination>
      </el-header>
      <el-main></el-main>
    </el-container>
  </div>
</template>
<script>
  import DepMana from './basic/DepMana.vue'
  import Pagination from './basic/Pagination.vue'
  export default{
    data(){
      return {
        depId: "",
        deps: [],
        employees:[],
        pageInfo:[],
        pageSize:10,
        currentPage:1,
        total:0,
        salaryTableVisible:false,
        emp:{
          address:'',
          beginContract:'',
          beginDate:'',
          birthday:'',
          contractTerm:'',
          conversionTime:'',
          department:{
            id:'',
            name:''
          },
          email:'',
          endContract:'',
          engageForm:'',
          gender:'',
          id:'',
          idCard:'',
          jobLevel:'',
          name:'',
          nation:'',
          nativePlace:'',
          notWorkDate:'',
          phone:'',
          politicsStatus:'',
          posName:'',
          position:'',
          salary:{
            accumulationFundBase:'',
            accumulationFundPer:'',
            allSalary:'',
            basicSalary:'',
            bonus:'',
            createDate:'',
            id:'',
            lunchSalary:'',
            medicalBase:'',
            medicalPer:'',
            name:'',
            pensionBase:'',
            pensionPer:'',
            trafficSalary:''
          },
          school:'',
          specialty:'',
          tiptopDegree:'',
          wedlock:'',
          workAge:'',
          workID:'',
          workState:''
        }
      }
    },
    components: {
      'dep-mana': DepMana,
      'pagination':Pagination
    },
    methods: {
      loadDeps(){
        var _this = this;
        this.getRequest("/salary/table/deps").then(resp=> {
          if (resp && resp.status == 200) {
            _this.deps = resp.data;
          }
        })
      },
      saveSalaryTableInfo(){
        var _this = this;
        this.postRequest("/salary/table/saveSalaryTableInfo",{
          salary:emp.salary,
          id:emp.id,
          name:emp.name,
          workID:emp.workID,
          departmentId:emp.departmentId
        }).then(resp=>{
          if(resp && resp.status == 200){

          }
        })
        this.salaryTableVisible = true;
      },
      setSalaryTable(emp){
        this.salaryTableVisible = true;
        if(emp.salary && emp.department){
          this.emp = emp;
        }else{
          this.emp.name=emp.name;
          this.emp.department = emp.department;
          this.emp.phone = emp.phone;
          this.emp.workID = emp.workID;
        }
        console.log(emp);
      },
      setPageSize(pageSize){
        this.pageSize = pageSize;
        var _this = this;
        this.postRequest("/employee/basic/employeeByDepartmentId",{
          departmentId:this.depId,
          pageSize:this.pageSize,
          currentPage:this.currentPage
        }).then(resp=>{
          _this.loading = false;
          if(resp && resp.status == 200){
            this.employees = resp.data.list;
            this.total = resp.data.total;
            console.info(resp.data);
          }
        })
      },
      setCurrentPage(currentPage){
        this.currentPage = currentPage;
        var _this = this;
        this.postRequest("/employee/basic/employeeByDepartmentId",{
          departmentId:this.depId,
          pageSize:this.pageSize,
          currentPage:this.currentPage
        }).then(resp=>{
          _this.loading = false;
          if(resp && resp.status == 200){
            this.employees = resp.data.list;
            this.total = resp.data.total;
            console.info(resp.data);
          }
        })
      },
      getDepartmentId(){
        var _this = this;
        this.postRequest("/employee/basic/employeeByDepartmentId",{
          departmentId:this.depId,
          pageSize:this.pageSize,
          currentPage:this.currentPage
        }).then(resp=>{
          _this.loading = false;
          if(resp && resp.status == 200){
            this.employees = resp.data.list;
            this.total = resp.data.total;
            console.info(resp.data);
          }
        })
      }
    },
    mounted:function () {
      this.loadDeps();
    }
  }
</script>



